.navbar {
    border-bottom: 1px solid rgb(199, 199, 199);
}

.navbar a {
    /* color: black !important; */
}

.navbar .navbar-brand {
    font-family: 'Cinzel Decorative', cursive;
    font-size: 1.8rem;
    font-weight: bold;
}

.navbar .items {
    /* font-family: Helvetica, sans-serif; */
    /* font-weight: normal; */
    /* font-size: 1rem; */
    min-width: 600px;
    justify-content: space-around;
}

.navbar .items a:hover {
    /* color: rgb(17, 109, 255) !important; */
}

.navbar .social-links {
    font-size: 2rem;
    flex-direction: row;
}

.navbar .social-links a {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0;
    padding-bottom: 0;
}

.navbar .social-links a:hover {
    /* color: rgb(17, 109, 255) !important; */
}

.navbar .social-links a:first-of-type {
    padding-left: 0;
}

.navbar .social-links a:last-of-type {
    padding-right: 0;
}

/**************** About Me ****************/

.about {
    /* padding-bottom: 400px; */
}

.about h1 {
    font-size: 80px;
}

.about p {
    font-size: 20px;
}

@media (max-width: 960px) {
    .about h1 {
        font-size: 9vw;
    }
    .about p {
        font-size: calc(16px + 0.5vw);
    }
}

@media (max-width: 480px) {
    .about h1 {
        font-size: 14vw;
    }
    .about p {
        font-size: 7vw;
    }
}

img {
    -webkit-filter: drop-shadow(0 0 5px grey);
    filter:         drop-shadow(0 0 5px grey);
}

